import { request } from 'utils';

const prefix = '/api/categories';

export const getRecruitingSchool = paramSearch =>
  request({
    prefix,
    url: '/School',
    method: 'GET',
    params: { ...paramSearch },
    data: {},
  });

export const getRecruitingSchoolById = id =>
  request({
    prefix,
    url: '/School/GetById',
    method: 'GET',
    params: { id },
    data: {},
  });

export const createRecruitingSchool = async values => {
  await request({
    prefix,
    url: `/School`,
    method: 'POST',
    params: {},
    data: { ...values },
  });
};

export const updateRecruitingSchool = async values => {
  await request({
    prefix,
    url: '/School',
    method: 'PUT',
    params: {},
    data: { ...values },
  });
};

export const deleteRecruitingSchool = id =>
  request({
    prefix,
    url: '/School',
    method: 'DELETE',
    params: { id },
    data: {},
  });



