import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    const queryParams = getQueryParams();
    delete queryParams._time;
    form.setFieldsValue(queryParams);
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Mã trường">
              {getFieldDecorator('id', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Tên trường">
              {getFieldDecorator('label', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" title="Tìm kiếm" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default SearchBar;



