import React, { Component } from 'react';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, message } from 'antd';
import { getRecruitingSchool, deleteRecruitingSchool } from 'services/recruitingSchool';
import { getQueryParams } from 'utils/urlUtil';
import SearchBar from './SearchBar';

class RecruitingSchool extends Component {
  state = { reload: 0, paramSearch: {} };

  componentWillMount() {
    this.handleChangeParamSearch();
  }

  componentWillReceiveProps() {
    this.handleChangeParamSearch();
  }

  handleChangeParamSearch() {
    const paramSearch = getQueryParams();
    this.setState({ paramSearch });
  }

  handleDelete = async id => {
    try {
      await deleteRecruitingSchool(id);
      message.success('Xóa thành công');
      this.setState({
        reload: this.state.reload + 1,
      });
    } catch (err) {
      console.log(err);
    }
  };

  render() {
    const { paramSearch } = this.state;

    const columns = [
      {
        title: 'Mã trường',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên trường',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.DANH_MUC__TUYEN_SINH__MA_TRUONG__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];

    return (
      <Card title="Danh sách mã trường tuyển dụng">
        <CartHeadAction>
          <Link to={routeConstant.DANH_MUC__TUYEN_SINH__MA_TRUONG__NHAP_DU_LIEU}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <SearchBar />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              columns={columns}
              paramSearch={paramSearch}
              service={getRecruitingSchool}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RecruitingSchool />
  </Page>
);



